

import TreeViewInternal, * as TV from "react-accessible-treeview"; // https://dgreene1.github.io/react-accessible-treeview/
import './TreeView.css'

import { BsFolder2, BsFolder2Open } from "react-icons/bs";

const folder = {
  name: "",
  children: [
    {
      name: "src",
      children: [{ name: "index.js" }, { name: "styles.css" }],
    },
    {
      name: "node_modules",
      children: [
        {
          name: "react-accessible-treeview",
          children: [{ name: "index.js" }],
        },
        { name: "react", children: [{ name: "index.js" }] },
      ],
    },
    {
      name: ".npmignore",
    },
    {
      name: "package.json",
    },
    {
      name: "webpack.config.js",
    },
  ],
};

const data = TV.flattenTree(folder);

export default function TreeView() {
  return (
    <div className="directory app-panel">
      <TreeViewInternal
        data={data}
        aria-label="directory tree"
        nodeRenderer={({
          element,
          isBranch,
          isExpanded,
          getNodeProps,
          level,
        }) => (
          <div {...getNodeProps()} style={{ paddingLeft: 20 * (level - 1) }}>
            {isBranch ? (
              <FolderIcon isOpen={isExpanded} />
            ) : (
              <FileIcon filename={element.name} />
            )}

            {element.name}
          </div>
        )}
      />
    </div>
  );
}

const FolderIcon = ({ isOpen }) =>
  isOpen ? (
    <BsFolder2Open color="e8a87c" className="icon" />
  ) : (
    <BsFolder2 color="e8a87c" className="icon" />
  );

const FileIcon = ({ filename }) => {
  const extension = filename.slice(filename.lastIndexOf(".") + 1);
  switch (extension) {
    case "js":
      return <BsFolder2 color="black" className="icon" />;
    case "css":
      return <BsFolder2 color="turquoise" className="icon" />;
    case "json":
      return <BsFolder2 color="black" className="icon" />;
    case "npmignore":
      return <BsFolder2 color="red" className="icon" />;
    default:
      return null;
  }
};